entraNo2-Medium.ttf');\n\tfont-weight: 500;\n}\n@font-face {\n\tfont-family: Centra;\n\tsrc: url('../assets/font/CentraNo2-Book.ttf');\n\tfont-weight: 400;\n}\n\n/************ Default Css ************/\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-behavior: smooth;\n  scroll-padding-top: 75px;\n}\n\nbody {\n  font-weight: 400;\n  overflow-x: hidden;\n  position: relative;\n  background-color: #121212 !important;\n  color: #fff !important;\n  font-family: 'Centra', sans-serif !important;\n}\n\nh1, h2, h3, h4, h5, h6 {\n  margin: 0;\n  padding: 0;\n  line-height: normal;\n}\n\np, a, li, button, ul {\n  margin: 0;\n  padding: 0;\n  line-height: normal;\n  text-decoration: none;\n}\n\na:hover {\n  text-decoration: none;\n}\n\nimg {\n  width: 100%;\n  height: auto;\n}\n\nbutton {\n  border: 0;\n  background-color: transparent;\n}\n\ninput:focus, textarea:focus, select:focus {\n  outline: none;\n}\n\n@media (min-width:1700px) {\n\tmain .container {\n\t\tmax-width: 100%;\n\t\tpadding: 0 150px;\n\t}\n}\n\np.success {\n\tcolor: green;\n}\n\np.danger {\n\tcolor: red;\n}\n/************ Navbar Css ************/\nnav.navbar {\n  padding: 18px 0;\n  position: fixed;\n  width: 100%;\n  top: 0;\n  z-index: 9999;\n  transition:  0.32s ease-in-out;\n}\nnav.navbar.scrolled {\n  padding: 0px 0;\n  background-color: #121212;\n}\nnav.navbar a.navbar-brand {\n    width: 9%;\n}\nnav.navbar .navbar-nav .nav-link.navbar-link {\n  font-weight: 400;\n  color: #fff !important;\n  letter-spacing: 0.8px;\n  padding: 0 25px;\n  font-size: 18px;\n  opacity: 0.75;\n}\nnav.navbar .navbar-nav a.nav-link.navbar-link:hover,\nnav.navbar .navbar-nav a.nav-link.navbar-link.active {\n    opacity: 1;\n}\nspan.navbar-text {\n    display: flex;\n    align-items: center;\n}\n.social-icon {\n    display: inline-block;\n    margin-left: 14px;\n}\n.social-icon a {\n    width: 42px;\n    height: 42px;\n    background: rgba(217, 217, 217, 0.1);\n    display: inline-flex;\n    border-radius: 50%;\n    margin-right: 6px;\n    align-items: center;\n    justify-content: center;\n    line-height: 1;\n    border: 1px solid rgba(255, 255, 255, 0.5);\n}\n.social-icon a::before {\n    content: \"\";\n    width: 42px;\n    height: 42px;\n    position: absolute;\n    background-color: #ffffff;\n    border-radius: 50%;\n    transform: scale(0);\n\ttransition: 0.3s ease-in-out;\n}\n.social-icon a:hover::before {\n    transform: scale(1);\n}\n.social-icon a img {\n\twidth: 40%;\n\tz-index: 1;\n\ttransition: 0.3s ease-in-out;\n}\n.social-icon a:hover img {\n\tfilter: brightness(0) saturate(100%) invert(0%) sepia(7%) saturate(98%) hue-rotate(346deg) brightness(95%) contrast(86%);\n}\n.navbar-text button {\n  font-weight: 700;\n  color: #fff;\n  border: 1px solid #fff;\n  padding: 18px 34px;\n  font-size: 18px;\n  margin-left: 18px;\n\tposition: relative;\n\tbackground-color: transparent;\n\ttransition: 0.3s ease-in-out;\n}\n.navbar-text button span {\n  z-index: 1;\n}\n.navbar-text button::before {\n  content: \"\";\n  width: 0%;\n  height: 100%;\n  position: absolute;\n  background-color: #fff;\n  left: 0;\n  top: 0;\n  z-index: -1;\n  transition: 0.3s ease-in-out;\n}\n.navbar-text button:hover {\n  color: #121212;\n}\n.navbar-text button:hover::before {\n\tcontent: \"\";\n\twidth: 100%;\n\theight: 100%;\n\tposition: absolute;\n}\nnav.navbar .navbar-toggler:active,\nnav.navbar .navbar-toggler:focus {\n\toutline: none;\n    box-shadow: none;\n}\nnav.navbar .navbar-toggler-icon {\n  width: 24px;\n  height: 17px;\n  background-image: none;\n  position: relative;\n  border-bottom: 2px solid #fff;\n  transition: all 300ms linear;\n  top: -2px;\n}\nnav.navbar .navbar-toggler-icon:focus {\n\tborder-bottom: 2px solid #fff;\n}\nnav.navbar .navbar-toggler-icon:after,\nnav.navbar .navbar-toggler-icon:before {\n\twidth: 24px;\n\tposition: absolute;\n\theight: 2px;\n\tbackground-color: #fff;\n\ttop: 0;\n\tleft: 0;\n\tcontent: '';\n\tz-index: 2;\n\ttransition: all 300